import Header from "../components/layout/Header";
import Footer from "../components/layout/Footer";
import PageHeader from "../components/shared/PageHeader";
import NewsCard from "../components/shared/NewsCard";
import { Newspaper, Search, Tag } from "lucide-react";
import { NEWS, CATEGORIES } from "@/lib/data";
import type { Metadata } from "next";

export const metadata: Metadata = {
    title: "Actualités Automobiles Algérie | DzairAuto",
    description: "Toutes les actualités du marché automobile algérien.",
};

const tags = [
    "Fiat Algérie", "Hyundai", "Prix voitures", "Importation",
    "Moins de 3 ans", "Usines", "Renault", "Peugeot", "Toyota", "Douanes"
];

export default function ActualitesPage() {
    return (
        <div className="min-h-screen bg-gray-50">
            <Header />

            <PageHeader
                icon={Newspaper}
                badge="Restez informé"
                title="ACTUALITÉS"
                highlight="AUTOMOBILES"
                description="Les dernières nouvelles du marché automobile algérien"
            />

            {/* Categories & Search */}
            <section className="py-4 bg-white shadow-sm sticky top-[104px] z-30">
                <div className="max-w-7xl mx-auto px-4">
                    <div className="flex flex-col md:flex-row gap-4 items-center justify-between">
                        <div className="flex flex-wrap gap-2">
                            {CATEGORIES.map((cat, index) => (
                                <button
                                    key={cat.slug}
                                    className={`px-4 py-2 rounded-full text-sm font-medium transition-colors ${index === 0
                                            ? "bg-[#D32F2F] text-white"
                                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                                        }`}
                                >
                                    {cat.name}
                                </button>
                            ))}
                        </div>
                        <div className="relative w-full md:w-72">
                            <input
                                type="text"
                                placeholder="Rechercher un article..."
                                className="w-full pl-10 pr-4 py-2 bg-gray-50 border border-gray-200 rounded-lg focus:outline-none focus:ring-2 focus:ring-[#D32F2F]/20"
                            />
                            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
                        </div>
                    </div>
                </div>
            </section>

            {/* Featured Article */}
            <section className="py-10">
                <div className="max-w-7xl mx-auto px-4">
                    <h2 className="text-lg font-bold text-gray-900 mb-4 flex items-center gap-2">
                        <span className="w-2 h-2 bg-[#D32F2F] rounded-full animate-pulse"></span>
                        À la une
                    </h2>
                    <NewsCard article={NEWS[0]} featured />
                </div>
            </section>

            {/* Articles Grid */}
            <section className="py-10 bg-white">
                <div className="max-w-7xl mx-auto px-4">
                    <h2 className="text-lg font-bold text-gray-900 mb-6">Derniers articles</h2>
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
                        {NEWS.map((article) => (
                            <NewsCard key={article.id} article={article} />
                        ))}
                    </div>

                    {/* Load More */}
                    <div className="text-center mt-10">
                        <button className="px-10 py-4 bg-gray-100 text-gray-700 rounded-full font-semibold hover:bg-gray-200">
                            Charger plus d'articles
                        </button>
                    </div>
                </div>
            </section>

            {/* Tags Cloud */}
            <section className="py-10 bg-gray-50">
                <div className="max-w-7xl mx-auto px-4">
                    <h2 className="text-lg font-bold text-gray-900 mb-6 text-center flex items-center justify-center gap-2">
                        <Tag className="w-5 h-5 text-[#D32F2F]" />
                        Sujets populaires
                    </h2>
                    <div className="flex flex-wrap justify-center gap-3">
                        {tags.map((tag) => (
                            <button
                                key={tag}
                                className="px-4 py-2 bg-white text-gray-700 rounded-full text-sm font-medium hover:bg-[#D32F2F] hover:text-white transition-colors shadow-sm"
                            >
                                {tag}
                            </button>
                        ))}
                    </div>
                </div>
            </section>

            <Footer />
        </div>
    );
}
