import { ReactNode } from "react";
import { LucideIcon } from "lucide-react";

interface PageHeaderProps {
    icon: LucideIcon;
    badge: string;
    title: string;
    highlight: string;
    description: string;
}

export default function PageHeader({ icon: Icon, badge, title, highlight, description }: PageHeaderProps) {
    return (
        <section className="bg-[#1a1a2e] text-white py-12">
            <div className="max-w-7xl mx-auto px-4">
                <div className="flex items-center gap-2 mb-3">
                    <Icon className="w-5 h-5 text-[#D32F2F]" />
                    <span className="text-[#D32F2F] font-semibold text-sm uppercase">{badge}</span>
                </div>
                <h1 className="text-4xl font-bold mb-2">
                    {title} <span className="text-[#D32F2F]">{highlight}</span>
                </h1>
                <p className="text-gray-400">{description}</p>
            </div>
        </section>
    );
}
